// src/utils/web3.js   
import Web3 from 'web3';

let web3;

if (window.ethereum) {
  //  Ethereum provider  MetaMask
  web3 = new Web3(window.ethereum);
  window.ethereum.enable(); //    
} else {
  //    
  const provider = new Web3.providers.HttpProvider('http://localhost:8545');
  web3 = new Web3(provider);
}

export default web3;